						<text v-if="activeSearchType == 2">‰∏™‰∫∫‰ΩúËÄÖÂú®Ë¥©ÂîÆÂ®ÉÁâ©ÂâçÂü∫Êú¨ÈÉΩÊòØÂúàÂÜÖÁé©ÂÆ∂ÔºåÂú®ËÆæËÆ°ÊñπÈù¢Ëä±ÁöÑÂøÉÊÄùÂæàÂ§ö„ÄÇ</text>
						<text v-if="activeSearchType == 3">ÂõΩÂ§ñÂ®ÉÁ§æËµ∑Ê≠•ËæÉÊó©ÔºåÈ£éÊ†ºËÆæËÆ°‰πüÊØîËæÉÂ§öÊ†∑Âåñ„ÄÇ</text>
