</template>

<script setup>
	import {
		onLoad,
		onReachBottom,
		onPullDownRefresh,
	} from "@dcloudio/uni-app"
	import {
		ref,
		computed
	} from 'vue';
	import {
		websiteUrl,
		wechatSignLogin,
		getUserInfo,
		global,
	} from "../../common/config.js";

	//ËΩÆÊí≠ÂõæÁªÑ‰ª∂
	import sdp from '@/components/swiper-dynamic-bullets/swiper-dynamic-bullets.vue';
	let brandsList = ref([]);
	let data = ref({})


	// ÂÆö‰πâÊ∏∏Ê†áÂèòÈáè
	let cursor = ref('') // Â≠òÂÇ®ÂàÜÈ°µÊ∏∏Ê†á
	let showLoadding = ref(false)

	// Á≠õÈÄâÁõ∏ÂÖ≥‰ª£Á†Å
	const tabs = ref([{
