			value: 1
		},
		{
			label: '‰∏™‰∫∫‰ΩúËÄÖ',
			value: 2
		},
		{
			label: 'Â§ñÂõΩÂ®ÉÁ§æ',
			value: 3
		},
	]);
	// ÈªòËÆ§ÈÄâ‰∏≠‰∏≠ÂõΩÂ®ÉÁ§æ
	const activeSearchType = ref(1);

	//ËΩÆÊí≠
	var swiperIndex = ref(0)
	const systemInfo = uni.getSystemInfoSync();
	// const statusBarHeight = ref(systemInfo.statusBarHeight + 15);
	// console.log("Áä∂ÊÄÅÊ†èÈ´òÂ∫¶" + statusBarHeight.value)

	//Êî∂ÂΩïBrandsÊÄªÊï∞
	var totalBrand = ref(0)

	//ÂàÜÈ°µÁõ∏ÂÖ≥ÂèòÈáè
	const page = ref(1)
	const pageSize = ref(10)
	const hasMore = ref(true)
	const loading = ref(false)

	// ÂõæÈÄèÈÉ®ÂàÜ
	let newsList = ref([])
	const newsPage = ref(1)
	const newsPageSize = ref(10)
	const newsHasMore = ref(true)
	const newsLoading = ref(false)

	// Êñ∞Â¢ûÁÉ≠Èó®Êê≠ÈÖçÁõ∏ÂÖ≥ÂèòÈáè
	let hotList = ref([])
	const hotPage = ref(1)
	const hotPageSize = ref(10)
	const hotHasMore = ref(true)
	const hotLoading = ref(false)

	// Ê†ëÊ¥ûÁõ∏ÂÖ≥ÂèòÈáè
	let treeholeList = ref([])
	const treeholePage = ref(1)
	const treeholePageSize = ref(10)
	const treeholeHasMore = ref(true)
	const treeholeLoading = ref(false)


	// Êñ∞Â¢ûÂàáÊç¢Áä∂ÊÄÅ
	const activeTab = ref('news')
	// ÂàáÊç¢ÈÄâÈ°πÂç°ÊñπÊ≥ï
	const switchTab = (tab) => {
		activeTab.value = tab
		// ËøôÈáåÂèØ‰ª•Ê∑ªÂä†ÂàáÊç¢Êó∂Âä†ËΩΩÂØπÂ∫îÊï∞ÊçÆÁöÑÈÄªËæë

	}


	// Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØ
	const navBarHeight = computed(() => {
		// #ifdef MP-WEIXIN
		const menuButtonInfo = uni.getMenuButtonBoundingClientRect()
		// Á°Æ‰øùÁä∂ÊÄÅÊ†èÈ´òÂ∫¶Â≠òÂú®ÔºàiOSÂèØËÉΩ‰∏∫0Ôºâ
		const statusBarHeight = systemInfo.statusBarHeight || 32
		return menuButtonInfo.bottom + menuButtonInfo.top - 2 * statusBarHeight
		// #endif
		return 0
	})
	// ËÆ°ÁÆóÈ°∂ÈÉ®Âç†‰ΩçÈ´òÂ∫¶
	const headerHeight = computed(() => {
		// Â∞èÁ®ãÂ∫èÈúÄË¶ÅÂåÖÂê´Áä∂ÊÄÅÊ†èÂíåÂØºËà™Ê†èÈ´òÂ∫¶
		// #ifdef MP-WEIXIN
		return navBarHeight.value + 'px'
		// #endif

		// // H5/App‰ΩøÁî®ÂÆâÂÖ®Âå∫ÂüüËÆ°ÁÆó
		// return `calc(constant(safe-area-inset-top) + 20px)` // ÈªòËÆ§ÂÄº20px‰Ωú‰∏∫fallback
		// #ifdef H5 || APP
		// Áõ¥Êé•Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶ÔºàiOS ÂÆâÂçìÈÄöÁî®Ôºâ
		const statusBarHeight = systemInfo.statusBarHeight || 0
		return `${statusBarHeight}px` // 44px ‰∏∫ iOS ÂØºËà™Ê†èÊ†áÂáÜÈ´òÂ∫¶
		// #endif
	})

	const onShareAppMessage = () => ({
		title: 'BJDÂ®ÉÂúà‰Ω†ÊÉ≥Áü•ÈÅìÁöÑËøôÈáåÈÉΩÊúâ~',
		path: '/pages/news/news',
		imageUrl: '/static/share',
		success(res) {
			console.log('ÂàÜ‰∫´ÊàêÂäü', res)
		},
		fail(err) {
			console.log('ÂàÜ‰∫´Â§±Ë¥•', err)
		},
		mp: {
			wxpath: '/pages/index/index.html'
		}
	})
	// ‰∏ãÊãâÂà∑Êñ∞Â§ÑÁêÜ
	onPullDownRefresh(async () => {
		try {
			await refreshData();
			uni.stopPullDownRefresh();
		} catch (error) {
			uni.stopPullDownRefresh();
			uni.showToast({
				title: 'Âà∑Êñ∞Â§±Ë¥•',
				icon: 'none'
			});
		}
	});

	// Áªü‰∏ÄÂà∑Êñ∞ÊñπÊ≥ï
	const refreshData = async () => {
		showLoadding.value = true
		// Á≠âÂæÖ0.3s
		setTimeout(async()=> {
		
			// Ê†πÊçÆÂΩìÂâçÈÄâÈ°πÂç°Âà∑Êñ∞ÂØπÂ∫îÊï∞ÊçÆ
			const refreshActions = {
				'brands': () => {
					page.value = 1;
					brandsList.value = [];
					hasMore.value = true;
					getBrands(true)
			
				},
				'news': () => {
					newsPage.value = 1;
					newsList.value = [];
					newsHasMore.value = true;
					getNews(true)
			
				},
				'hot': () => {
			
					hotPage.value = 1;
					hotList.value = [];
					hotHasMore.value = true;
					getHotCollocations(true)
				},
				'second': () => {
					treeholePage.value = 1;
					treeholeList.value = [];
					treeholeHasMore.value = true;
					getTreeholeList(true)
				}
			};
			
			await refreshActions[activeTab.value]();
			// getArticles(); // ÂßãÁªàÂà∑Êñ∞ËΩÆÊí≠Âõæ
		}, 800)
		
	};


	function jump2collocationSquare() {
		uni.navigateTo({
			url: "/pages/collocation_square/collocation_square"
		})
	}

	const handleBannerClick = (item) => {
		// console.log(item)
		uni.navigateTo({
			url: `/pages/article_detail/article_detail?id=${item.id}`
		})
	}

	function jump2saleNews(item) {
		uni.navigateTo({
			url: "/pages/sale_news/sale_news?id=" + item.id + "&brand_id=" + item.brand_id
		})
	}
	// Êñ∞Â¢ûËé∑ÂèñÊñáÁ´†ÂàóË°®ÊñπÊ≥ï
	const getArticles = () => {
		uni.request({
			url: websiteUrl + '/articles',
			data: {
				page: 1,
				page_size: 10,
				status: 1 // Âè™Ëé∑ÂèñÂ∑≤ÂèëÂ∏ÉÊñáÁ´†
			},
			success: (res) => {
				if (res.data.status === 'success') {
					// ËøáÊª§ÊúâÊïàbannerÂπ∂Êõ¥Êñ∞Êï∞ÊçÆ
					data.value = res.data.data.list
				}
			},
			fail: (err) => {
				console.error('Ëé∑ÂèñBannerÂ§±Ë¥•:', err)
				data.value = {}
			}
		})
	}

	function onChange(e) {
		this.swiperIndex.value = e.detail.current
	}

	const handleTabClick = (value) => {
		// ÂàáÊç¢Á≠õÈÄâÁ±ªÂûãÊó∂ÈáçÁΩÆÂàÜÈ°µÂíåÊï∞ÊçÆ
		if (activeSearchType.value === value) {
			activeSearchType.value = null; // ÁÇπÂáªÂ∑≤ÈÄâ‰∏≠ÁöÑtabÂèñÊ∂àÁ≠õÈÄâ
		} else {
			activeSearchType.value = value;
		}
		page.value = 1;
		hasMore.value = true;
		brandsList.value = [];
		cursor.value = ''; // ÈáçÁΩÆÊ∏∏Ê†á
		getBrands();
	}

	function jump2userWhenNotAnonymous(item) {
		if (item.is_anonymous == 1) {
			return
		}
		uni.navigateTo({
			url: '/pages/user_page/user_page?uid=' + item.uid
		})
	}

	function copyUrl(item) {
		let url = "http://m.dogdogdoll.com/#/" + "pages/treehole_detail/treehole_detail?id=" + item.id
		// Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø
		uni.setClipboardData({
			data: url,
			success: function() {
				uni.showToast({
					title: 'Â§çÂà∂ÈìæÊé•ÊàêÂäü',
					icon: 'none'
				});
			}
		});
	}

	const getNews = async (isRefresh = false) => {
		if (isRefresh) newsPage.value = 1;
		if (!newsHasMore.value || newsLoading.value) return

		newsLoading.value = true
		showLoadding.value = true
		uni.request({
			url: websiteUrl + '/sale-news',
			data: {
				page: newsPage.value,
				pageSize: newsPageSize.value
			},
			success: (res) => {
				const newData = res.data.data.news_list
				if (newData.length === 0) {
					newsHasMore.value = false
					return
				}
				newsList.value = [...newsList.value, ...newData]
				newsHasMore.value = res.data.data.news_list.length === newsPageSize.value
				newsPage.value++
			},
			fail: (err) => {
				console.log(err)
				uni.showToast({
					title: 'Âä†ËΩΩÂ§±Ë¥•',
					icon: 'none'
				})
			},
			complete: () => {
				newsLoading.value = false
				showLoadding.value = false
			}
		})
	}

	function jump2treeholeDetail(item) {

		uni.navigateTo({
			url: '/pages/treehole_detail/treehole_detail?id=' + item.id
		})
	}

	// Ëé∑ÂèñÁÉ≠Èó®Êê≠ÈÖçÊï∞ÊçÆ
	const getHotCollocations = async (isRefresh = false) => {
		if (isRefresh) hotPage.value = 1;
		if (!hotHasMore.value || hotLoading.value) return

		hotLoading.value = true
		showLoadding.value = true
		uni.request({
			url: websiteUrl + '/hot-collocation',
			data: {
				page: hotPage.value,
				pageSize: hotPageSize.value
			},
			success: (res) => {
				const newData = res.data.data.collocation_relation_list
				if (newData.length === 0) {
					hotHasMore.value = false
					return
				}
				hotList.value = [...hotList.value, ...newData.map(item => ({
					...item,
				}))]
				hotHasMore.value = hotList.value.length < res.data.data.total
				hotPage.value++
			},
			fail: (err) => {
				console.log(err)
				uni.showToast({
					title: 'Âä†ËΩΩÂ§±Ë¥•',
					icon: 'none'
				})
			},
			complete: () => {
