		})
	}



	const getBrands = async (isRefresh = false) => {
		if (isRefresh) {
			page.value = 1;
			brandsList.value = [];
			hasMore.value = true;
			cursor.value = ''; // ÈáçÁΩÆÊ∏∏Ê†á
		}

		if (!hasMore.value || loading.value) return;

		loading.value = true;
		showLoadding.value = true

		// ÊûÑÈÄ†ËØ∑Ê±ÇÂèÇÊï∞
		const params = {
			pageSize: pageSize.value
		};

		// Ê∑ªÂä†Ê∏∏Ê†áÊàñÊêúÁ¥¢Á±ªÂûãÂèÇÊï∞
		if (cursor.value) {
			params.cursor = cursor.value;
		} else if (activeSearchType.value) {
			params.searchType = activeSearchType.value;
		}

		uni.request({
			url: websiteUrl + '/brands-info-list',
			data: params,
			success: (res) => {
				if (res.data.status !== 'success') {
					uni.showToast({
						title: 'Âä†ËΩΩÂ§±Ë¥•: ' + res.data.message,
						icon: 'none'
					});
					return;
				}

				const response = res.data.data;
				const newData = response.data;
				console.log("newData:", newData)
				// Êõ¥Êñ∞Ê∏∏Ê†á
				cursor.value = response.next_cursor || '';

				if (newData.length === 0) {
					hasMore.value = false;
					return;
				}

				brandsList.value = [...brandsList.value, ...newData];
				hasMore.value = response.has_more;
			},
			fail: (err) => {
				console.log(err);
				uni.showToast({
					title: 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
					icon: 'none'
				});
			},
			complete: () => {
				loading.value = false;
				showLoadding.value = false;
			}
		});
	}


	// Ëé∑ÂèñÊ†ëÊ¥ûÂàóË°®
	const getTreeholeList = async (isRefresh = false) => {
		if (isRefresh) treeholePage.value = 1;
		if (!treeholeHasMore.value || treeholeLoading.value) return

		treeholeLoading.value = true
