				showLoadding.value = false
			}
		})
	}

	// Â§ÑÁêÜÂèëÂ∏É
	function handlePublish() {
		// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
		if (!global.isLogin) {
			uni.showModal({
				title: 'ÊèêÁ§∫',
				content: 'ÈúÄË¶ÅÁôªÂΩïÂêéÊâçËÉΩÂèëÂ∏ÉÊ†ëÊ¥û',
				success: res => {
					if (res.confirm) uni.navigateTo({
						url: '/pages/login/login'
					})
				}
			})
			return
		}

		uni.navigateTo({
			url: '/pages/treehole_publish/treehole_publish'
		})
	}

	// ÂõæÁâáÈ¢ÑËßà
	function previewImage(images, index) {

		uni.previewImage({
			current: index,
			urls: images
		})

	}

	// Â§ÑÁêÜÁÇπËµû
	function handleLike(item) {
		if (!global.isLogin) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			})
			return
		}
		let token = uni.getStorageSync('token')
		if (!token) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			})
			return
		}
		uni.request({
			url: websiteUrl + (item.has_liked ? '/with-state/unlike' : '/with-state/add-like'),
			method: 'POST',
			header: {
				Authorization: token,
			},
			data: {
				id: item.id,
				type: 5
			}, // 5Ë°®Á§∫Ê†ëÊ¥ûÁ±ªÂûã
			success: (res) => {
				if (res.data.status === "success") {
					item.has_liked = !item.has_liked
					item.like_count += item.has_liked ? 1 : -1
				} else {
					uni.showD
				}
			}
		})
	}


	// Êó∂Èó¥Ê†ºÂºèÂåñÊñπÊ≥ï
	const formatTime = (timestamp) => {
		const date = new Date(timestamp * 1000)
		return `${date.getFullYear()}-${date.getMonth()+1}-${date.getDate()}`
	}

	// ÂàùÂßãÂåñÂä†ËΩΩ
	onLoad(() => {
		getUserInfo()
		getBrands()
		getNews() // Êñ∞Â¢ûÂàùÂßãÂåñÂä†ËΩΩ
		getHotCollocations()
		//Âä†ËΩΩÊ†ëÊ¥û
		getTreeholeList()
		//Ëé∑ÂèñËΩÆÊí≠Âõæ
		getArticles()
	})



	// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
	onReachBottom(() => {
		if (activeTab.value === 'brands' && hasMore.value) {
			getBrands();
		} else if (activeTab.value === 'news' && newsHasMore.value) {
			getNews();
		} else if (activeTab.value === 'second' && treeholeHasMore.value) {
			getTreeholeList();
		}
	})
